// Defaults
$pad : 1rem;
$pad-and-half : $pad*1.5;
$pad-double : $pad*2;
$pad-half : $pad/2;
$pad-quarter :$pad/4;

// Menu bar
$menuBarWidthSmall: 2rem;
$menuBarWidthLarge: 3.16rem;
$menuBarWidthExtraLarge: 7.9rem;
$menuBarWidthExtraExtraLarge: 10.9rem;

// Menu bar icons
$menuIconSizeRatioSmall: 0.6;
$menuIconSizeRatioLarge: 0.48;
$menuIconSizePaddingRatioSmall: (1 - $menuIconSizeRatioSmall) / 2;
$menuIconSizePaddingRatioLarge: (1 - $menuIconSizeRatioLarge) / 2;

// Max page widths
$maxPageWidth: (
	// px: 1920px,
	em: 120em,
	// px: 1920px,
	rem: 96rem,
);


// Theme variables that can be overwritten for English site
$menuBarBg: getColor(hemelblauw light) !default;
$menuBarBgStop1: getColor(hemelblauw light) !default;
$menuBarBgStop2: getColor(mintgroen default) !default;
$menuBarOpacity: 0.9 !default;
$menuBarButtonColor: getColor(hemelblauw darkest);

$brandBarTopBg: getColor(hemelblauw default) !default;

$footerBg: getColor(hemelblauw lighter);
$footerColophonBg: getColor(hemelblauw light);