.accordion {
	margin-bottom: 2rem;

	&-toggles {
		text-align: right;
		line-height: 1.2;
		padding-bottom: 1rem;
	}

	&-toggle {
		&:last-child {
			margin-left: 0.5rem;
		}
		&--button {
			@include cleanButton;
			@include underline;
			color: getColor(hemelblauw default);

			@include hoverEtc {
				color: getColor(hemelblauw grijscontrast);
				fill: getColor(hemelblauw grijscontrast);
			}
		}

		&--inactive {
			color: getColor(grijs grijs7);
		}
	}
}

.acc {
	@include theme((background-color: background, border-top-color: foreground), '.accordion');
	border-top: 1px solid;

	&.isOpened{
		background-color: white;

		@include theme((border-top-color: foreground), '.accordion');
	}

	&-openCloseIcon{
		svg{
			height: 1rem;
			width: 1rem;
		}
		cursor: pointer;
		transition: transform 0.3s;
		fill: getColor(hemelblauw default);

		.isOpened &{
			transform: rotate(135deg);
			fill: getColor(hemelblauw grijscontrast);
		}
	}

	&-openCloseAll{
		display: block;
		margin-bottom: 1rem;
		margin-left: auto;

		&-button{
			display: flex;
			align-items: center;
			fill: getColor(hemelblauw default);
			@include cleanButton;

			svg{
				fill: getColor(hemelblauw default);
				width: 1.5rem;
				height: 1.5rem;
			}

			&-text{
				margin-right: 10px;
			}

			@include hoverEtc{
				color: getColor(hemelblauw darkest);
				fill: getColor(hemelblauw darkest);
			}
		}
	}

	&-header{
		margin-top: 1rem;
		margin-bottom: 1rem;
		fill: getColor(hemelblauw default);
		@include hoverEtc{
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
		}
	}

	&-titles{
		margin-top: 1rem;
		margin-bottom: 1rem;
		color: getColor(hemelblauw default);
		flex: 1 1 0;

		@include hoverEtc{
			color: getColor(hemelblauw darkest);

			+ .acc-openCloseIcon {
				fill: getColor(hemelblauw darkest);
			}
		}

		.isOpened &{
			font-weight: bold;
		}
	}

	&-title{
		margin-right: 0.5rem;
		margin-top: 0;
		margin-bottom: 0;

		.isOpened & {
			@include theme((color: foreground), '.accordion');
		}
	}

	&-content{
		&-body{
			// Make sure all kind of different half column blocks in accordions
			// are aligned correctly
			.column--half {
				margin-top: 1rem;
				margin-bottom: 1rem;
			}
			.directRegelen-list {
				width: 100%;
			}
		}
		&-heading{
			margin: 0;
		}
		&-title{
			@include cleanButton;
			width: 100%;
			display: block;

			@include hoverEtc{
				@include theme((background: background), '.accordion');
			}
		}
	}

	&-smoothOpen{
		padding-top: $quarterSpace;
		padding-bottom: $halfSpace;
	}

}